import { motion, AnimatePresence } from "framer-motion";
import React, { useState, useEffect } from "react";
import "../styles/main.css";

const LoadingScreen = ({ onComplete }) => {
  const [isComplete, setIsComplete] = useState(false);
  const [showLastFrame, setShowLastFrame] = useState(false);
  const [lastFrameLoaded, setLastFrameLoaded] = useState(false);
  const [gifLoaded, setGifLoaded] = useState(false);

  useEffect(() => {
    //      ( resetLoading()  )
    if (process.env.NODE_ENV === 'development') {
      window.resetLoading = () => {
        sessionStorage.removeItem('lastLoadingTime');
        window.location.reload();
      };
    }
    
    //      (5   )
    const lastLoadingTime = sessionStorage.getItem('lastLoadingTime');
    const now = Date.now();
    
    if (lastLoadingTime && (now - parseInt(lastLoadingTime)) < 5000) {
      //    
      setIsComplete(true);
      setTimeout(() => onComplete(), 100);
      return;
    }

    // GIF     
    const gifImg = new Image();
    const lastFrameImg = new Image();
    
    let loadStartTime = Date.now();
    
    const handleImagesLoaded = () => {
      if (gifLoaded && lastFrameLoaded) {
        const loadTime = Date.now() - loadStartTime;
        const minLoadingTime = 3000; //    (2000  3500)
        const gifDuration = 2500; // GIF   (2500  3000)
        
        //       
        const waitTime = Math.max(0, minLoadingTime - loadTime);
        
        setTimeout(() => {
          setShowLastFrame(true);
          
          //     
          setTimeout(() => {
            setIsComplete(true);
            sessionStorage.setItem('lastLoadingTime', Date.now().toString());
            setTimeout(() => onComplete(), 800); // 300  800
          }, 1200); // 500  1200
        }, waitTime);
      }
    };
    
    gifImg.onload = () => {
      setGifLoaded(true);
      handleImagesLoaded();
    };
    
    lastFrameImg.onload = () => {
      setLastFrameLoaded(true);
      handleImagesLoaded();
    };
    
    //  
    gifImg.onerror = () => {
      console.warn('GIF  ,   ');
      setGifLoaded(true);
      handleImagesLoaded();
    };
    
    lastFrameImg.onerror = () => {
      console.warn('   ');
      setLastFrameLoaded(true);
      handleImagesLoaded();
    };
    
    gifImg.src = `${process.env.PUBLIC_URL}/images/loading.gif`;
    lastFrameImg.src = `${process.env.PUBLIC_URL}/images/last_frame.jpg`;
    
    //     (5)
    const maxWaitTimer = setTimeout(() => {
      if (!isComplete) {
        setIsComplete(true);
        sessionStorage.setItem('lastLoadingTime', Date.now().toString());
        onComplete();
      }
    }, 5000);
    
    return () => {
      clearTimeout(maxWaitTimer);
    };
  }, [onComplete, isComplete, gifLoaded, lastFrameLoaded]);

  return (
    <AnimatePresence>
      {!isComplete && (
        <motion.div 
          className="loading-screen"
          initial={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          transition={{ duration: 1.5 }}
        >
          {/* GIF  */}
          {gifLoaded && !showLastFrame && (
            <img 
              src={`${process.env.PUBLIC_URL}/images/loading.gif?t=${Date.now()}`}
              alt=" "
              style={{
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
                objectFit: 'cover',
                zIndex: 1
              }}
              onLoad={() => console.log('GIF  ')}
            />
          )}
          
          {/*    */}
          {showLastFrame && lastFrameLoaded && (
            <motion.img 
              src={`${process.env.PUBLIC_URL}/images/last_frame.jpg`}
              alt=" "
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ duration: 0.6 }}
              style={{
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
                objectFit: 'cover',
                zIndex: 2
              }}
            />
          )}
          
          <div className="loading-content" style={{ position: 'relative', zIndex: 3 }}>
            
          </div>
        </motion.div>
      )}
    </AnimatePresence>
  );
};

export default LoadingScreen; 