import React, { useState, useEffect } from "react";
import LoadingScreen from "./components/LoadingScreen";
import Header from "./components/Header";
import CoupleInfo from "./components/CoupleInfo";
import Countdown from "./components/Countdown";
import Gallery from "./components/Gallery";
// import TabSection from "./components/TabSection";
import WeddingInfo from "./components/WeddingInfo";
// import GuestSnap from "./components/GuestSnap";
import Directions from "./components/Directions";
import AccountInfo from "./components/AccountInfo";
import Footer from "./components/Footer";
import ScrollToTop from "./components/ScrollToTop";
import "./styles/main.css";

function App() {
  const [isLoading, setIsLoading] = useState(true);
  const [showHeroText, setShowHeroText] = useState(false);

  const handleLoadingComplete = () => {
    setIsLoading(false);
    //    hero  
    setTimeout(() => {
      setShowHeroText(true);
    }, 200);
  };

  //       
  useEffect(() => {
    const lastLoadingTime = sessionStorage.getItem('lastLoadingTime');
    const now = Date.now();
    
    if (lastLoadingTime && (now - parseInt(lastLoadingTime)) < 5000) {
      //    
      setIsLoading(false);
      setShowHeroText(true);
    }
  }, []);

  return (
    <div className="App">
      {/* Hero   ,    */}
      <Header isLoading={isLoading} showHeroText={showHeroText} />
      
      {/*    */}
      {isLoading && <LoadingScreen onComplete={handleLoadingComplete} />}
      
      {/*       */}
      {!isLoading && (
        <>
          <CoupleInfo />
        
          
          {/* <TabSection /> */}
          <WeddingInfo />
          <Countdown />
          <Gallery />
          {/* <Timeline /> */}
          
          {/* <GuestSnap /> */}
          <Directions />
          <AccountInfo />
          <Footer />
        </>
      )}
      
      {/*   -   */}
      <ScrollToTop />
    </div>
  );
}

export default App;
